(function() {
  'use strict';

  angular
    .module('lappWeb', [
      'ngAnimate',
      'ngCookies',
      'ngTouch',
      'ngSanitize',
      'ngMessages',
      'ngAria',
      'ngResource',
      'ui.router',
      'ui.bootstrap',
      'toastr',
      'ksSwiper',
      'cgBusy'
    ])

    .config(['$stateProvider', '$urlRouterProvider', '$locationProvider', '$httpProvider', function ($stateProvider, $urlRouterProvider, $locationProvider, $httpProvider) {
      $urlRouterProvider
        .otherwise('/');

      $locationProvider.html5Mode(true);

      //CORS
      $httpProvider.defaults.useXDomain = true;
      delete $httpProvider.defaults.headers.common['X-Requested-With'];

      $httpProvider.defaults.headers.common['ZUMO-API-VERSION'] = '2.0.0'; // add the application key
      //$httpProvider.defaults.headers.common['X-ZUMO-APPLICATION'] = 'myapplicationkey'; // add the application key
      //$httpProvider.defaults.headers.common['Content-Type'] = 'Application/json';
    }])

    .factory('httpRequestInterceptor', function ($cookies) {
      return {
        request: function (config) {
          //config.headers.Authorization = 'ZUMO-API-VERSION 2.0.0';
          return config;
        }
      };
    });

})();

(function() {
	'use strict';

	angular
		.module('lappWeb')
		.directive('lappNavbar', lappNavbar);

	lappNavbar.$inject = ['MainService', '$rootScope'];
	/** @ngInject */
	function lappNavbar(MainService, $rootScope) {
		var directive = {
			restrict: 'E',
			templateUrl: 'app/components/navbar/navbar.html',
			scope: {
				creationDate: '=',
				cartPrice: '=?cartPrice'
			},
			controller: NavbarController,
			controllerAs: 'nav',
			bindToController: true
		};

		return directive;

		/** @ngInject */
		function NavbarController(MainService, $rootScope) {
			var vm = this;
			// vm.cartPrice = 0;
			// $rootScope.$on('productAdded', function(event) {
			

			// });

			//Categorias menu
			MainService.catalogs().then(function(resp) {
				vm.categories = resp;
			})


			var winW, winH, winScr, _isresponsive, _ismobile = navigator.userAgent.match(/Android/i) || navigator.userAgent.match(/webOS/i) || navigator.userAgent.match(/iPhone/i) || navigator.userAgent.match(/iPad/i) || navigator.userAgent.match(/iPod/i);

			/*========================*/
			/* 02 - page calculations */
			/*========================*/
			function pageCalculations() {
				winW = window.innerWidth;
				winH = window.innerHeight;
				if (angular.element(document.querySelector('.menu-button')).is(':visible')) _isresponsive = true;
				else _isresponsive = false;


			}

			/*=================================*/
			/* 03 - function on document ready */
			/*=================================*/
			pageCalculations();
			var $searchDropDownOverflow = angular.element(document.querySelector('.search-drop-down .overflow'));
			var $searchDropDown = angular.element(document.querySelector('.search-drop-down'));

			if ($searchDropDownOverflow.length && !_ismobile) {
				$searchDropDown.addClass('active');
				$searchDropDownOverflow.jScrollPane();
				$searchDropDown.removeClass('active');
			}
			if (_ismobile) $('body').addClass('mobile');

			/*============================*/
			/* 04 - function on page load */
			/*============================*/
			pageCalculations();
			angular.element(document.querySelector('#loader-wrapper')).fadeOut();
			angular.element(document.querySelector('body')).addClass('loaded');

			/*==============================*/
			/* 05 - function on page resize */
			/*==============================*/
			function resizeCall() {
				pageCalculations();

				$('.navigation:not(.disable-animation)').addClass('disable-animation');
			}
			if (!_ismobile) {
				$(window).resize(function() {
					resizeCall();
				});
			} else {
				window.addEventListener("orientationchange", function() {
					resizeCall();
				}, false);
			}

			//desktop menu
			$('nav>ul>li').on('mouseover', function() {
				if (!_isresponsive) {
					$(this).find('.submenu').stop().fadeIn(300);
				}
			});

			$('nav>ul>li').on('mouseleave', function() {
				if (!_isresponsive) {
					$(this).find('.submenu').stop().fadeOut(300);
				}
			});

			//responsive menu
			$('nav li .fa').on('click', function() {
				if (_isresponsive) {
					$(this).next('.submenu').slideToggle();
					$(this).parent().toggleClass('opened');
				}
			});

			angular.element(document.querySelector('.submenu-list-title .toggle-list-button')).on('click', function() {
				if (_isresponsive) {
					$(this).parent().next('.toggle-list-container').slideToggle();
					$(this).parent().toggleClass('opened');
				}
			});

			angular.element(document.querySelector('.menu-button')).on('click', function() {
				$('.navigation.disable-animation').removeClass('disable-animation');
				$('body').addClass('opened-menu');
				$(this).closest('header').addClass('opened');
				$('.opened .close-header-layer').fadeIn(300);
				closePopups();
				return false;
			});

			angular.element(document.querySelector('.close-header-layer, .close-menu')).on('click', function() {
				$('.navigation.disable-animation').removeClass('disable-animation');
				$('body').removeClass('opened-menu');
				$('header.opened').removeClass('opened');
				$('.close-header-layer:visible').fadeOut(300);
			});

			//toggle menu block for "everything" template
			angular.element(document.querySelector('.toggle-desktop-menu')).on('click', function() {
				$('.navigation').toggleClass('active');
				$('nav').removeClass('disable-animation');
				$('.search-drop-down').removeClass('active');
			});

			/*sidebar menu*/
			angular.element(document.querySelector('.sidebar-navigation .title')).on('click', function() {
				if ($('.sidebar-navigation .title .fa').is(':visible')) {
					$(this).parent().find('.list').slideToggle(300);
					$(this).parent().toggleClass('active');
				}
			});

			/*search drop down*/
			angular.element(document.querySelector('.search-drop-down .title')).on('click', function() {
				$(this).parent().toggleClass('active');
			});

			angular.element(document.querySelector('.search-drop-down .category-entry')).on('click', function() {
				var thisDropDown = $(this).closest('.search-drop-down');
				thisDropDown.removeClass('active');
				thisDropDown.find('.title span').text($(this).text());
			});



			angular.element(document.querySelector('.departmets-drop-down .title')).on('click', function() {
				$(this).parent().find('.list').slideToggle(300);
				$(this).toggleClass('active');
			});

			angular.element(document.querySelector('.departmets-drop-down')).on('mouseleave', function() {
				$(this).find('.list').slideUp(300);
				$(this).find('.title').removeClass('active');
			});


		}
	}

})();

(function() {
  'use strict';

  angular
    .module('lappWeb')
    .directive('lappFooter', lappFooter);

  /** @ngInject */
  function lappFooter() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/footer/footer.html',
      scope: {
          creationDate: '='
      },
      controller: FooterController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function FooterController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function(){
  'use strict';

  angular
    .module('lappWeb')
    .factory('AuthFactory', function Auth($location, $rootScope, $http, User, $cookies, $q) {
      var currentUser = {};
      if($cookies.get('token')) {
        currentUser = User.get();
      }

      return {

        /**
         * Authenticate user and save token
         *
         * @param  {Object}   user     - login info
         * @param  {Function} callback - optional
         * @return {Promise}
         */
        login: function(user, callback) {
          var cb = callback || angular.noop;
          var deferred = $q.defer();

          $http.post('/auth/local', {
            email: user.email,
            password: user.password
          }).
          success(function(data) {
            $cookies.put('token', data.token);
            currentUser = User.get();
            deferred.resolve(data);
            return cb();
          }).
          error(function(err) {
            this.logout();
            deferred.reject(err);
            return cb(err);
          }.bind(this));

          return deferred.promise;
        },

        /**
         * Delete access token and user info
         *
         * @param  {Function}
         */
        logout: function() {
          $cookies.remove('token');
          currentUser = {};
        },

        /**
         * Create a new user
         *
         * @param  {Object}   user     - user info
         * @param  {Function} callback - optional
         * @return {Promise}
         */
        createUser: function(user, callback) {
          var cb = callback || angular.noop;

          return User.save(user,
            function(data) {
              $cookies.put('token', data.token);
              currentUser = User.get();
              return cb(user);
            },
            function(err) {
              this.logout();
              return cb(err);
            }.bind(this)).$promise;
        },

        /**
         * Change password
         *
         * @param  {String}   oldPassword
         * @param  {String}   newPassword
         * @param  {Function} callback    - optional
         * @return {Promise}
         */
        changePassword: function(oldPassword, newPassword, callback) {
          var cb = callback || angular.noop;

          return User.changePassword({ id: currentUser._id }, {
            oldPassword: oldPassword,
            newPassword: newPassword
          }, function(user) {
            return cb(user);
          }, function(err) {
            return cb(err);
          }).$promise;
        },

        /**
         * Gets all available info on authenticated user
         *
         * @return {Object} user
         */
        getCurrentUser: function() {
          return currentUser;
        },

        /**
         * Check if a user is logged in
         *
         * @return {Boolean}
         */
        isLoggedIn: function() {
          return currentUser.hasOwnProperty('role');
        },

        /**
         * Waits for currentUser to resolve before checking if user is logged in
         */
        isLoggedInAsync: function(cb) {
          if(currentUser.hasOwnProperty('$promise')) {
            currentUser.$promise.then(function() {
              cb(true);
            }).catch(function() {
              cb(false);
            });
          } else if(currentUser.hasOwnProperty('role')) {
            cb(true);
          } else {
            cb(false);
          }
        },

        /**
         * Check if a user is an admin
         *
         * @return {Boolean}
         */
        isAdmin: function() {
          return currentUser.role === 'admin';
        },

        /**
         * Get auth token
         */
        getToken: function() {
          return $cookies.get('token');
        }
      };
    });
})();

(function() {

    angular
        .module('lappWeb')
        .service('ProductService', ['RESOURCE_API', '$http', '$q', '$filter', function(RESOURCE_API, $http, $q, $filter) {

            var service = {
                get: get,
                post: post,
                put: put,
                drop: drop,
                getDetail: getDetail
            };
            return service;

            function get() {
              var deferred = $q.defer();
              $http.get(RESOURCE_API + 'products/' + id)
                .success(function(products) {
                  deferred.resolve(products);
                })
                .error(function(data, status, headers, config) {
                  // data is always undefined here when there is an error
                  console.error('Error fetching feed:', data);
                });
              return deferred.promise;
            }

            function post(data) {
                var deferred = $q.defer();
                $http.post('/api/v1/planning/maintenance', data).success(function(evt) {
                    deferred.resolve(evt);
                });
                return deferred.promise;
            }

            function put(id) {
                var deferred = $q.defer();
                $http.put('/api/v1/planning/maintenance/:id', {
                    id: id
                }).success(function(evt) {
                    deferred.resolve(evt);
                });
                return deferred.promise;
            }

            function drop(id) {
                var deferred = $q.defer();
                $http.delete('/api/v1/planning/maintenance/:id', {
                    id: id
                }).success(function(data) {
                    deferred.resolve(evt);
                });
                return deferred.promise;
            }

            function getDetail(id) {
              var deferred = $q.defer();
              //$http.get(RESOURCE_API + 'products/' + id)
              $http.get('./assets/data/catalogs.json')
                .success(function(product) {
                  deferred.resolve(product);
                })
                .error(function(data, status, headers, config) {
                  // data is always undefined here when there is an error
                  console.error('Error fetching feed:', data);
                });
              return deferred.promise;
            }


        }]);
})();

(function() {
  'use strict';

  angular
    .module('lappWeb')
    .controller('ProductDetailController', ProductDetailController);

  ProductDetailController.$inject = ['ProductService', 'CartService', '$rootScope', '$stateParams'];

  /** @ngInject */
  function ProductDetailController(ProductService, CartService, $rootScope, $stateParams) {

    /*angular.element(document.querySelector('.fixed-div')).css({
      'padding-top': angular.element(document.querySelector('header')).outerHeight(true)
    });*/

    var vm = this;
    vm.quantity = 1;
    var settings = JSON.parse(localStorage.getItem('settings'));
    vm.policies = _.find(settings, {
      'key': 'Policies'
    }).value;

    vm.spinnerDown = function(product) {

      vm.quantity = CartService.removeProductUnity(product,vm.quantity);
    };

    vm.spinnerUp = function(product) {
      vm.quantity = CartService.addProductUnity(product,vm.quantity);
    };

    vm.AddProduct = function(product, quantity) {
      vm.quantity = 1;
      CartService.add(product, quantity);
    };

    if ($stateParams.id) {
      ProductService.getDetail($stateParams.id).then(function(resp) {
        var result;
        var b = angular.forEach(resp, function(value, key) {
          angular.forEach(value.products, function(element) {
            if (element.id === $stateParams.id) {
              result = element;
            }
          });
        });

        vm.product = result;
      });
    }





  }
})();

