(function() {
  'use strict';

  angular
    .module('lappWeb')
    .controller('ShopCategoriesController', ShopCategoriesController);

  ShopCategoriesController.$inject = ['ShopService', '$rootScope',  '$stateParams'];

  /** @ngInject */
  function ShopCategoriesController(ShopService, $rootScope, $stateParams) {
    var vm = this;

    vm.events = {
      getSubCategory: function(id){
        console.log(id);
        ShopService.getSubCategory(id).then(function(resp){
          vm.catalogsProduct = resp[0].products;
          //REfresh
        });
      }
    };

    if ($stateParams.id) {
      ShopService.get($stateParams.id).then(function(resp){
        vm.catalogs = resp;
        vm.catalogsProducts = []
        _(vm.catalogs).forEach(function(catalog) {
          vm.catalogsProducts = vm.catalogsProducts.concat(catalog.products);
        });
      });
    }




    vm.quantity = 1;
    var settings = JSON.parse(localStorage.getItem('settings'));
    vm.policies = _.find(settings, {
      'key': 'Policies'
    }).value;

    vm.spinnerDown = function() {
      if (vm.quantity > 1) {
        vm.quantity--;
      }
    };

    vm.spinnerUp = function() {
      if (vm.quantity < vm.productDetail.quantity) {
        vm.quantity++;
      }
    };

    vm.AddProduct = function(product,quantity) {
      product.quantityToAdd=quantity;
      $rootScope.$emit('productAdded', product);
    };



  }
})();

