(function() {

    angular
        .module('lappWeb')
        .service('CartService', ['RESOURCE_API', '$http', '$q', '$filter', '$rootScope', function(RESOURCE_API, $http, $q, $filter, $rootScope) {

            var service = {
                add: add,
                remove: remove,
                get: get,
                addProductUnity: addProductUnity,
                removeProductUnity: removeProductUnity
            };
            return service;

            function get() {
                return $rootScope.cart.products
            }

            function add(product, quantity) {
                product.quantityToAdd = quantity;

                if (angular.isUndefined($rootScope.cart)) {
                    $rootScope.cart = {
                        products: []
                    };
                }

                var productExists = _.find($rootScope.cart.products, {
                    'id': product.id
                });

                if (productExists && productExists.quantityToAdd + product.quantityToAdd <= product.quantity) {
                    productExists.quantityToAdd += product.quantityToAdd;
                } else if (!productExists) {
                    var productToAdd = angular.copy(product);
                    $rootScope.cart.products.push(productToAdd);
                }
                calculateSubTotal();
                $rootScope.$emit('productAdded');
            }

            function remove(id) {
                _.remove($rootScope.cart.products, {
                    'id': id
                });

                calculateSubTotal()
            }

            function calculateSubTotal() {
                var priceToAdd = 0;
                _.forEach($rootScope.cart.products, function(value) {
                    priceToAdd += (value.value * value.quantityToAdd);

                });

                $rootScope.cart.subTotal = priceToAdd;
            };

            function addProductUnity(product, quantity) {

                if (quantity < product.quantity) {
                    quantity++;
                }

                var productExists = _.find($rootScope.cart.products, {
                    'id': product.id
                });

                if (productExists && productExists.quantityToAdd + product.quantityToAdd <= product.quantity) {
                    productExists.quantityToAdd = quantity;
                    calculateSubTotal();
                }

                return quantity;
            };

            function removeProductUnity(product, quantity) {

                if (quantity > 1) {
                    quantity--;
                }
                var productExists = _.find($rootScope.cart.products, {
                    'id': product.id
                });

                if (productExists && productExists.quantityToAdd + product.quantityToAdd <= product.quantity) {
                    productExists.quantityToAdd =quantity;
                    calculateSubTotal();
                }
                return quantity
            };


        }]);
})();
(function() {
  'use strict';

  angular
    .module('lappWeb')
    .controller('CartDetailController', CartDetailController);

  CartDetailController.$inject = ['CartService', '$rootScope', '$stateParams'];

  /** @ngInject */
  function CartDetailController(CartService, $rootScope, $stateParams) {

    angular.element(document.querySelector('.information-blocks')).css({
      'padding-top': angular.element(document.querySelector('header')).outerHeight(true)
    });

    var vm = this;

    vm.removeFromCart = function(id) {
      CartService.remove(id);
    }

    vm.spinnerDown = function(product) {

      product.quantityToAdd = CartService.removeProductUnity(product,product.quantityToAdd);
    };

    vm.spinnerUp = function(product) {
      product.quantityToAdd = CartService.addProductUnity(product, product.quantityToAdd);
    };

  }
})();
